.setting-wrap{
  float: left;
  width: 580px;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.07);
  padding: 15px;
  box-sizing: border-box;
  @include breakpoint(max-width 770px){
    width: calc(100% - 220px);
  }
}

.setting-wrap__panel{
  width: 100%;
  float: left;
  position: relative;
  padding: 20px 0;
  box-sizing: border-box;
}

.setting-wrap__panel_pic{
  width: 39px;
  height: 39px;
  float: left;
  position: relative;
  margin-right: 15px;
  img{
    size: 100%;
  }
}

.setting-wrap__panel_name{
  float: left;
  position: relative;
  text-align: left;
  min-width: 60%;
  box-sizing: border-box;
  padding-top: 11px;
  @include breakpoint(max-width 770px){
    min-width: 56%;
  }
  h4{
    margin: 0;
    float: left;
    text-align: left;
    color: #333;
    font-size: 14px;
    font-weight: 300;

  }
}

.setting-wrap__panel_name-edit{
  float: left;
  position: relative;
  display: inline-block;
  margin-left: 15px;
  &:hover i{
    color: $green;
  }
  i{
    font-size: 16px;
    font-weight: 400;
    color: #707070;
  }
}

.setting-wrap__panel-tools{
  float: right;
  width: 140px;
  position: relative;
  text-align: right;
  a{
    display: inline-block;
    float: none;
    position: relative;
    margin: 0 10px;
    &:hover i{
      color: $green;
    }
    i{
      color: #707070;
      font-size: 16px;
      font-weight: 400;
      line-height: 40px;
    }
  }
}

.setting-wrap__nav{
  width: 100%;
  float: left;
  display: block;
  background-color: #fff;
  height: initial;
  ul{
    display: block;
    float: left;
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0;
    li{
      float: left;
      display: inline-block;
      width: 50%;
      margin: 0;
      border-top: 1px solid $green;
      a{
        height: 41px;
        display: inline-block;
        background-color: #fff;
        width: 100%;
        float: left;
        span{
          width: 100%;
          float: left;
          text-align: center;
          font-size: 14px;
          font-weight: 300;
          line-height: 41px;
          font-style: normal;
          font-stretch: normal;
          letter-spacing: normal;
          color: #333;
          box-sizing: border-box;
        }
      }

    }
  }
  .page__tabs_active{

   background-color: $green ;
   height: 41px;
   span{
     color: #fff;
   }
  }

}


.setting-wrap__content{
  width: 100%;
  float: left;
  position: relative;
  .current{
    display: block;
  }
}


.setting-wrap__content_item{
  width: 100%;
  float: left;
  position: relative;
  display: none;
  // text-align: center;
}

.setting-wrap__content-thems{
  padding: 30px 0 40px;
  box-sizing: border-box;
}

.setting-wrap__content-thems,
.setting-wrap__content-navigations,
.setting-wrap__content-design,
.setting-wrap__content-splesh,
.setting-wrap__content-icon{
  width: 100%;
  float: left;
  position: relative;

}


.setting-wrap__content-thems__item,
.setting-wrap__content-navigations__item,
.setting-wrap__content-design__item{
  width: 106px;
  height: 169px;
  float: left;
  position: relative;
  margin-right: 35px;
  // &:nth-of-type(4){
  //   margin-right: 0;
  // }
  &:last-of-type{
    margin-right: 0;
  }
  &:nth-of-type(8){
    margin-right: 0;
  }
  &:nth-of-type(12){
    margin-right: 0;
  }
  &:nth-of-type(16){
    margin-right: 0;
  }
  &:nth-of-type(20){
    margin-right: 0;
  }
  cursor: pointer;
  &:after{
    content: "";
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    bottom: -10px;
    background-color: transparent;
  }
  &:hover{
    &:after{
      content: "";
      background-color: #cdcdcd;
    }
  }
  img{
    size: 100%;
    // object-fit: cover;
  }
  @include breakpoint(max-width 770px){
    width: 100px;
    margin-right: 10px;
  }
}

.setting-wrap__content-thems__item.active:after{
  background-color: $green;
}
.setting-wrap__content-navigations__item.active:after{
  background-color: $green;
}

.setting-wrap__content-design__item.active:after{
  background-color: $green;
}



.setting-wrap__content-navigations__title,
.setting-wrap__content-design__title,
.setting-wrap__content-splesh__title,
.setting-wrap__content-icon__title{
  width: 100%;
  cursor: pointer;
  float: left;
  padding: 0 10px;
  box-sizing: border-box;
}


.toogle-title{
  color: #333;
  font-size: 14px;
  font-weight: 600;
  line-height: 50px;
  text-align: left;
  display: inline-block;
  border-top: 1px solid #ccc;
  position: relative;
  .arrow{
    float: right;
    width: 24px;
    height: 24px;
    background-color: #ccc;
    border-radius: 50%;
    right: -1px;
    margin-top: -11px;
    position: absolute;
    text-align: center;
    // transform: rotate(270deg);
    i{
      color: #333;
      display: inline-block;
      font-size: 11px;
      font-weight: 400;
      position: relative;
      top: -14px;
      left: 2px;
    }
    &:hover{
      background-color: $green;
      i{
        color: #fff;
      }
    }
  }
}

.arrow-active{
  transform: rotate(90deg);
  transition: all .7s ease;
}

.upload-button{
  float: left;
  width: 60%;
  margin-bottom: 10px;
  cursor: pointer;
  .upload+label{
    background-color: $green;
    color: #fff;
    border: 1px solid $green;
    margin: 0;
    width: 150px;
  }
  @include breakpoint(max-width 770px){
    width: 56%;
  }
}

.new-design__wrap{
  width: 100%;
  float: left;
  position: relative;
  margin-bottom: 30px;
}
.new-design__wrap,
.splesh__wrap,
.icon__wrap{
  width: 100%;
  float: left;
  position: relative;

}

.icon__wrap{
  .upload-button{
    width: 74%;
    .upload+label{
      float: left;
    }
    p{
      margin: 0;
      display: inline-block;
      float: left;
      color: #333;
      font-size: 12px;
      font-weight: 300;
      line-height: 38px;
      margin-left: 24px;
    }
    @include breakpoint(max-width 770px){
      width: 68%;
    }
  }
}

.new-design__pic{
  float: left;
  position: relative;
  width: 106px;
  height: 155px;
  margin-right: 30px;
  img{
    size: 100%;
  }
}

.splesh__pic{
  float: left;
  width: 158px;
  height: 187px;
  background-color: white;
  border: 1px solid #cdcdcd;
  margin-right: 30px;
  img{
    size: 100%;
  }
}

.icon__pic{
  size: 103px;
  float: left;
  position: relative;
  margin-right: 30px;
  img{
    size: 100%;
  }
}

.icons__wrap{
  float: left;
  width: 74%;
  position: relative;
  margin-top: 22px;
}
.icons__wrap_item{
  size: 31px;
  float: left;
  margin-right: 10px;
  img{
    size: 100%;
  }
}


.iphone-wrap{
  width: 262px;
  height: 532px;
  background-image: url("../img/iphone.png");
  background-size: contain;
  background-repeat: no-repeat;
  display: inline-block;
  float: right;
  position: relative;
  @include breakpoint(max-width 770px){
    width: 200px;
  }
}

.iphone-wrap__pic{
  width: 233px;
  height: 414px;
  position: absolute;
  left: 6%;
  margin-top: 22%;
  img{
    size: 100%;
  }
  @include breakpoint(max-width 770px){
    width: 178px;
    height: 317px;
  }
}


.toogle-content{
  width: 100%;
  float: left;
  // display: inline-block;
  display: none;
  padding-bottom: 50px;
  padding-top: 10px;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
}

.design__content{
  display: block;
  width: 100%;
  float: left;
  padding-bottom: 50px;
  padding-top: 10px;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
}


.category-menu{
  width: 160px;
  float: left;
  position: relative;
  margin-right: 40px;
  h3{
    color: #333;
    width: 100%;
    display: inline-block;
    text-align: left;
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    line-height: 36px;
    border-bottom: 1px solid #ccc;
  }
  ul{
    width: 100%;
    float: left;
    position: relative;
    margin: 0;
    padding: 0;
    li{
      width: 100%;
      text-align: left;
      list-style-type: none;
      display: inline-block;
      float: left;
      a{
        width: 100%;
        float: left;
        font-size: 14px;
        font-weight: 300;
        line-height: 36px;
        color: #333;
      }
    }
    li.active{
      a{
        color: #00aeaf;
      }
    }
  }
}


.category-content{
  width: calc(100% - 200px);
  float: left;
  position: relative;
}


.category-content__item{
  float: left;
  position: relative;
  margin-right: 15px;
  margin-bottom: 15px;
  width: 137px;
  min-height: 300px;
  &:hover p{
    border: 1px solid $green;
    border-top: none;
  }
  &:nth-of-type(4){
    margin-right: 0;
  }
  &:nth-of-type(8){
    margin-right: 0;
  }
  &:nth-of-type(12){
    margin-right: 0;
  }
  &:nth-of-type(16){
    margin-right: 0;
  }
  &:nth-of-type(20){
    margin-right: 0;
  }
  .thumb{
    width: 100%;
    height: 254px;
    position: relative;
    float: left;
    img{
      size: 100%;
    }
  }
  p{
    width: 100%;
    float: left;
    text-align: center;
    color: #333;
    margin: 0;
    display: inline-block;
    float: left;
    font-size: 14px;
    font-weight: 300;
    line-height: 36px;
    border: 1px solid transparent;
    border-top: none;
  }
}
