.contents{
  width: calc(100% - 241px);
  float: left;
  position: relative;
  padding: 30px 15px 30px 30px;
  box-sizing: border-box;
  background-color: #f8f8f8;
  margin-top: 2px;
  @include breakpoint(max-width 770px){
    width: 100%;
  }
}
.content-wrap{
  width: 100%;
  float: left;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.07);
  background-color: #fff;
  min-height: 446px;
}

.title-panel{
  width: 100%;
  float: left;
  background-color: $green;
  padding: 20px 25px;
  box-sizing: border-box;
  p{
    margin: 0;
    width: 100%;
    text-align: left;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
  }
}


.green-btn{
  min-width: 150px;
  height: 41px;
  background-color: #00aeaf;
  border-radius: 5px;
  padding: 0 15px;
  box-sizing: border-box;
  display: inline-block;
  float: right;
  font-size: 14px;
  font-weight: 300;
  line-height: 40px;
  text-align: center;
  color: #fff;
  border: 1px solid #00aeaf;
  outline: none;
  &:hover{
    background-color: #fff;
    border: 1px solid $green;
    color: $green;
  }
}
.white-btn{
  min-width: 150px;
  height: 41px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0 15px;
  box-sizing: border-box;
  display: inline-block;

  font-size: 14px;
  font-weight: 300;
  line-height: 40px;
  text-align: center;
  color: #333;
  &:hover{
    background-color: #fff;
    border: 1px solid $green;
    color: $green;
  }
}

.delete-btn{
  float: right;
  min-width: 138px;
  display: inline-block;
  height: 40px;
  background-color: #d81313;
  border: 1px solid #d81313;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 300;
  line-height: 40px;
  color: #fff;
  &:hover{
    border: 1px solid #d81313;
    background-color: #fff;
    color: #d81313;
  }
  @include breakpoint(max-width 770px){
    min-width: 90px;
  }
}
.breadcrumbs{
  width: 100%;
  float: left;
  position: relative;
  padding: 20px 0;
  box-sizing: border-box;
}

.breadcrumbs__list {
		list-style-type: none;
		float: left;
		padding: 0;
    margin: 0;
    // top: 4px;
    position: relative;
		li {
			display: inline-block;
			position: relative;
      font-size: 14px;
      line-height: 15px;
			color: $green;
		  font-weight: 400;
      margin: 0 10px 0 12px;
			a {
			  color: $green;
        font-weight: 400;
        position: relative;
        &:after{
          content: "";
          border-bottom: 1px dashed $green;
          position: absolute;
          width: 100%;
          left: 0;
          bottom: 0;
        }
			}
			&:first-of-type {
				margin-left: 0;
			}

			&:after {
				content: "â†’";
				position: absolute;
				right: -18px;
				bottom: 0;
			}
			&:last-of-type {
				&:after, &:before {
					content: none;
				}
			}

		}
	}


.custom-select{
  width: 150px;
  display: inline-block;
  position: relative;
  margin-left: 15px;
  margin-top: 25px;
}

.select{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  height: 40px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0 15px;
  box-sizing: border-box;
  display: inline-block;
  font-weight: 400;
  font-size: 14px;
  text-align: left;
  line-height: 40px;
  color: #333;
  cursor: pointer;
  z-index: 10;

  // @include breakpoint(max-width 660px){
  //   width: 100%;
  //   font-size: 19px;
  // }


}


.select__trigger{
  display: block;
  position: absolute;
  height: 40px;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  z-index: 20;
  i{
    color: #bbb;
    font-size: 11px;
    font-weight: 400;
    float: right;
    line-height: 40px;
  }
}

.anketa__select--option{
  display: none;
  position: absolute;
  left: 0;
  top: 35px;
  float: left;
  padding-top: 10px;
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
  z-index: 1;
  border: 1px solid #e5e5e5;
  border-top: none;
  border-radius: 5px;
  > a{
    display: block;
    position: relative;
    background-color: #fff;
    padding: 0 15px;
    width: 100%;
    box-sizing: border-box;
    display: inline-block;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    color: #333;
    line-height: 30px;
    @include breakpoint(max-width 660px){
      font-size: 15px;
    }
  }
}


